.story-maker-container {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 180px);
  max-width: 800px;
  margin: 0 auto;
}

.maker-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 2px solid #e0e0e0;
  background: white;

  h2 {
    margin: 0;
    font-size: 1.3rem;
    color: #333;
    flex: 1;
    text-align: center;
  }

  .date-info {
    font-size: 0.9rem;
    color: #667eea;
    font-weight: 600;
    min-width: 80px;
    text-align: right;
  }
}

.chat-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: #f8f9fa;
  overflow: hidden;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 15px;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-thumb {
    background: #667eea;
    border-radius: 4px;
  }
}

.message-wrapper {
  display: flex;
  animation: slideIn 0.3s ease-out;

  &.question {
    justify-content: flex-start;

    .message-bubble {
      background: white;
      border: 2px solid #667eea;
    }
  }

  &.answer {
    justify-content: flex-end;

    .message-bubble {
      background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
      color: white;
    }
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.message-bubble {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  max-width: 70%;
  padding: 15px 20px;
  border-radius: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);

  .message-icon {
    font-size: 24px;
    line-height: 1;
  }

  .message-content {
    flex: 1;
    line-height: 1.6;
    word-break: keep-all;
  }
}

.chat-input-area {
  display: flex;
  gap: 10px;
  padding: 20px;
  background: white;
  border-top: 2px solid #e0e0e0;
  align-items: flex-end;

  .full-width {
    flex: 1;
  }

  button {
    margin-bottom: 8px;
  }
}

.processing-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px;
  gap: 15px;
  background: white;
  border-top: 2px solid #e0e0e0;

  p {
    margin: 0;
    color: #667eea;
    font-weight: 500;
  }
}

@media (max-width: 768px) {
  .story-maker-container {
    height: calc(100vh - 120px);
  }

  .maker-header {
    padding: 15px;

    h2 {
      font-size: 1.1rem;
    }
  }

  .message-bubble {
    max-width: 85%;
    padding: 12px 15px;
  }

  .chat-input-area {
    padding: 15px;
  }
}
